@charset "UTF-8";
@import "../../bower_components/bootstrap-sass/assets/stylesheets/bootstrap/_variables.scss";
$btn-width:56px;
$sidebar-width:240px;
$win-header-height:43px;
$page-header-height:68px;

#keyPlugin{
  width:0;
  height:0;
  visibility:hidden;
  position:absolute;
}

html, body, .wrapper{
  width:100%;
  height:100%;
  position:relative;
  overflow:hidden;
}


/**
 * 顶部样式，包含：
 * 窗口控制按钮
 * 前进后退
 * 页面 nav 按钮
 */
.header{
  //-webkit-app-region:drag;
  position:fixed;
  z-index:999;
  width:100%;
  height:$win-header-height;
  background:url(../../dist/img/bg-header.png) repeat-x;

  .nav-control {
    padding:0 15px;
    height:100%;

    li {
      list-style:none;
      float:left;
      height:100%;
    }
    a {
      -webkit-app-region:no-drag;
      width: 36px;
      height:100%;
      display:block;
      background:center center no-repeat;
    }
    .btn-back {
      background-image:url(../../dist/img/btn-back.png);
    }
    .btn-back:hover {
      background-image:url(../../dist/img/btn-back-hover.png);
    }
    .btn-forward {
      background-image:url(../../dist/img/btn-forward.png);
    }
    .btn-forward:hover {
      background-image:url(../../dist/img/btn-forward-hover.png);
    }
  }
  .win-control{
    padding:0 15px;
    li{
      list-style:none;
      float:left;
    }
    a{
      -webkit-app-region:no-drag;
      padding:0 8px;
      display:block;
      color:#393939;
      font-weight:200;
      font-size:30px;
    }
  }

  .nav{
    -webkit-app-region:no-drag;
    float:right;
    height:100%;

    li{
      width:110px;
      height:100%;
      margin:0;
      text-align:center;
      font-size:12px;

      a{
        display:inline-block;
        height:100%;
        padding:14px 0 0 26px;
        color:#626262;
        background:no-repeat left center;
      }

      a:hover{
        color:#3a83d1;
      }
    }
    li.active{
      a, a:hover{
        color:#3a83d1;
        background-color:transparent;
      }
    }

    li.separator{
      height:12px;
      width:2px;
      background:#9d9d9d;
      margin:15px 10px;
      padding:0;
    }

    #nav-home{
      a{
        background-image:url(../../dist/img/icon-user.png);
      }
    }
    #nav-home a:hover, #nav-home.active a{
      background-image:url(../../dist/img/icon-user-hover.png);
    }
    #nav-secure{
      a{
        background-image:url(../../dist/img/icon-scan.png);
      }
    }
    #nav-secure a:hover, #nav-secure.active a{
      background-image:url(../../dist/img/icon-scan-hover.png);

    }
    #nav-device{
      a{
        padding-left:36px;
        background-image:url(../../dist/img/icon-ukey.png);
      }
    }
    #nav-device a:hover, #nav-device.active a{
      background-image:url(../../dist/img/icon-ukey-hover.png);
    }

    #nav-cert{
      a{
        background-image:url(../../dist/img/icon-cert2.png);
      }
    }
    #nav-cert a:hover, #nav-cert.active a{
      background-image:url(../../dist/img/icon-cert2-hover.png);
    }
    #nav-download{
      a{
        background-image:url(../../dist/img/icon-download.png);
      }
    }
    #nav-download a:hover, #nav-download.active a{
      background-image:url(../../dist/img/icon-download-hover.png);
    }
  }
}

#page-ct{
  position:absolute;
  top:$win-header-height;
  left:0;
  right:0;
  bottom:0;

  .tab-pane{
    width:100%;
    height:100%;
    background:url(../../dist/img/bg-blue.jpg) 0 0 repeat;
  }
}


.p-header {
  width:100%;
  height: $page-header-height;
  background: #f2f5fa;
  a {
    display:block;
    width: 370px;
    height: 100%;
    background: url("../../dist/img/logo.png") 15px center no-repeat;
  }
}

.p-content {
  width: 100%;
}


#page-home{
  iframe{
    width:100%;
    height:100%;
    border:0;
  }
}


/**
 * 设备与证书页面共享样式
 */
.key-form {
  width: 75%;
  min-width: 900px;
  margin: 100px auto;
  color: #fff;
  background:right 10px no-repeat;
  padding-bottom: 40px;
  font-size:16px;

  .left-ct {
    width: 550px;
    form{
      width:100%;
      padding: 0 20px;
    }
  }

  .h-separator {
    width:100%;
    height:60px;
    background:url(../../dist/img/h-separator.png) center center no-repeat;
  }

  .form-group{
    position:relative;
    height:35px;
    margin-bottom:20px;

    select, input{
      width:370px;
      height:100%;
      border-radius:2px;
      background:transparent;
      border:#ffffff 1px solid;
      padding-left:6px;
      line-height:35px;
      float: right;
    }
    .name{
      line-height:35px;
    }
    option {
      color: #333;
    }
  }
  button.btn {
    font-size:16px;
    width:106px;
    height:40px;
    background:#5ba6e6;
    padding:0;
  }
}


/**
 * 设备管理页面
 */
#page-device {
  .key-form {
    background:url(../../dist/img/icon-key.png) right 10px no-repeat;
    .left-ct {

      .tip{
        color:#e3e3e3;
        margin: 20px 0;
      }
    }
  }
}


/**
 * 证书管理页面
 */
#page-cert{

  .key-form {
    background:url(../../dist/img/icon-cert.png) right 10px no-repeat;
    .left-ct {
      form,.cert-detail{
        width:100%;
        padding: 0 10px;
      }
    }
  }

  .cert-detail{
    width:100%;
    padding:0;

    li{
      list-style:none;
      width:100%;
      padding-bottom:20px;
    }
    span {
      margin-left:4px;
    }

  }
}


/**
 * 安全检测页面
 */
#page-secure {
  .p-content {
    position:absolute;
    top: $page-header-height;
    left:0;
    right:0;
    bottom:0;
    background:#fff;
  }

  .detecting {
    width: 100%;
    position: relative;
    height: 100%;

    .top {
      position: absolute;
      top: 0;
      left: 0;
      height: 120px;
      width: 100%;
      padding: 0 120px 0 80px;
      background: #3689cf;
      border-bottom: solid 1px #d6d6d6;
      img {
        height: 85px;
        width: 85px;
        float: left;
        margin-top:20px;
        display: block;
      }

      .top-title {
        float: left;
        margin: 0;
        padding: 15px 20px;
        color: #fff;
        h3 {
          font-size: 24px;
        }
        p {
          font-size: 14px;
        }
      }
      .top-btns {
        float: right;
        height: 100%;
        overflow:hidden;
        button {
          font-size: 16px;
          height: 42px;
          width: 100px;
          background:#fff;
          margin-left: 15px;
          margin-top: (120px-42px)/2;
        }
        #start-btn {
          color: #09a400;
        }
        #cancel-btn {
          color: #ff4000;
          display:none;
        }
        #restart-btn {
          color: #ffa300;
          display:none;
        }
        #fix-all-btn {
          color: #09a400;
          display:none;
        }
      }
    }

    .detect-list {
      position: absolute;
      top: 120px;
      bottom:0;
      left:0;
      right:0;
      overflow-y: auto;
      padding: 0 120px 0 60px;

      h3 {
        width: 100%;
        overflow: hidden;
        font-size: 16px;
        padding: 0;
        img {
          margin: 0 10px 0 5px;
        }
      }

      ul {
        width: 100%;
        font-size: 12px;
        li {
          list-style:none;
          color:#a9a9a9;
          padding: 8px 0;
        }
        .btn {
          margin-left:10px;
          cursor:none;
        }
        p {
          margin: 10px 0 10px 1em;
          color: $brand-danger;
          line-height: 2em;
        }
      }
      .h-separator {
        background:#d6d6d7;
        height:1px;
        width:100%;
        margin: 20px 0;
      }
    }
  }

  .summary {
    width: 100%;
    height: 530px;
    position: absolute;
    left: 0;
    display: none;
    .top-left {
      float: right;
      width: 360px;
      height: 100%;
      p {
        margin-top: 10px;
        line-height: 22px;
      }
      button {
        margin: 75px auto;
      }
    }
  }
}

@import "download";