
#page-download{
  .p-content{
    position:absolute;
    top:68px;
    left:0;
    right:0;
    bottom:0;
    background:#fff;

    .sidebar{
      width:240px;
      height:100%;
      background:#3585CA;
      color:#ffffff;
      position:relative;
      float:left;
      ul{
        padding-top:20px;
        padding-left:0;
        li{
          position:relative;
          line-height:50px;
          width:100%;
          font-size:18px;
          img{
            margin-left:50px;
            margin-right:10px;
          }
          a{
            background:transparent;
            color:#ffffff;
            padding:0;
          }
        }
      }
      .nav-pills > li + li{
        margin-left:0px;
      }
      .active{
        background:#0B528E;
      }
    }

    .tab-content{
      top:0;
      .download-list{
        margin-left:240px;
        background:#fff;
        padding:0;
        li{
          list-style:none;
          height:84px;
          font-size:18px;
          padding-top:18px;
          padding-left:34px;
          border-bottom:1px solid #E4E4E4;
          position:relative;
          .left{
            width:100%;
            img{
              float:left;
              width:40px;
              height:50px;
              margin-right:16px;
            }
            span{
              font-size:16px;
              margin-right:10px;
            }

            .progress{
              width:352px;
              height:14px;
              border-radius:14px;
              margin-top:6px;

              .progress-bar{
                position:relative;
                span{
                  font-size:12px;
                  color:#4BAB2A;
                  position:absolute;
                  top:-2px;
                  left:100%;
                  margin-left:4px;
                }
              }
            }
          }

          .right{
            position:absolute;
            right:300px;
            top:30px;
            .open-dir, .delete-file,.execute-file{
              width:110px;
              height:36px;
              float:right;
              margin-right:30px;
              cursor:pointer;
              img{
                margin-right:12px;
              }
            }
            .cancel-dw, .downloading-control{
              width:110px;
              height:36px;
              float:right;
              font-size:16px;
              cursor:pointer;
              img{
                margin-right:12px;
              }
            }
          }
        }
      }

    }
    .footer{
      position:absolute;
      bottom:25px;
      left:240px;
      height:68px;
      line-height:68px;
      width:100%;
      border-top:1px solid #ECECEC;
      button{
        float:right;
        margin:18px 340px;
        background:#5BA6E6;
      }
    }
  }

}